import { NgModule } from '@angular/core';
import { Routes, RouterModule } from '@angular/router';
import { CourseHrComponent } from './course-hr/course-hr.component';
import { CoursePmComponent } from './course-pm/course-pm.component';
import { CourseRsComponent } from './course-rs/course-rs.component';
import { LandingComponent } from './landing/landing.component';

const routes: Routes = [
  { path: '', component: LandingComponent },
  { path: 'pm', component: CoursePmComponent },
  { path: 'hr', component: CourseHrComponent },
  { path: 'rs', component: CourseRsComponent },

];

@NgModule({
  imports: [RouterModule.forRoot(routes, {
    anchorScrolling: 'enabled',
    scrollPositionRestoration: 'enabled',
    relativeLinkResolution: 'legacy',
  })],
  exports: [RouterModule]
})
export class AppRoutingModule {

}
